.App {
  text-align: center;
  background-color: rgb(32, 157, 191);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

button{
  color: black;
  background-color: rgb(224, 218, 218);
  font-weight: 900;
  height: 40px;
  width: 100px;
border-radius: 20px;
text-align: center;
}
h1{
  position: absolute;
  left: 50px;
  top:200px;
  font-size: 3rem;
}
p{
  position: absolute;
  right: 20px;
  top: 300px;
  font-size: 2rem;
  color: #191c13;
  font-weight: 800;
  font-family: cursive;
}
P.quote{
  top: 500px;
  transform: skewY(-0deg);
}
a{
  top: 500px;
  transform: skewY(-0deg);
  left:30px;
  position: absolute;
  font-size: 2rem;
  color: #191c13;
  font-weight: 800;
  font-family: cursive;
}
